import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Auth from '@/pages/Auth'
// import Config from require（'../../config'）
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      // name: 'Auth',
      // component: Auth,
      redirect: '/index'
    },
    {
      path: '/auth',
      name: 'Auth',
      component: Auth
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    }
  ]
})



